@import "./../base/vars";
.questions {
  margin-top: 72px;
  &__subtitle {
    font-size: 30px;
    font-family: Lato-Black;
    color: $accentColor;
    text-align: center;
    margin-bottom: 24px;
  }
  &__title {
    font-size: 72px;
    font-family: Lato Bold;
    max-width: 788px;
    margin: 0 auto;
    padding-bottom: 72px;

    @media all and (max-width: 992px) {
      font-size: 40px;
      text-align: center;
    }
  }

  &__wrapper,
  &__wrap,
  &__form {
    display: flex;
    @media all and (max-width: 1172px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__form {
    flex-direction: column;
    margin-left: 140px;
    @media all and (max-width: 1312px) {
      margin-left: 50px;
    }
    @media all and (max-width: 1172px) {
      margin-top: 20px;
      right: 20px;
      position: relative;
    }
  }

  &__input_name,
  &__input_mail,
  &__textarea {
    width: 460px;
    border: none;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    font-size: 24px;
    font-family: Lato SemiBold;
    text-indent: 10px;
    box-shadow: 0px 4px 18px rgba(104, 104, 105, 0.35),
      inset 0px 1px 0px rgba(255, 255, 255, 0.3);

    @media all and (max-width: 480px) {
      max-width: 460px;
      width: 100%;
      font-size: 18px;
    }
  }
  &__input_name,
  &__input_mail {
    height: 72px;
    margin-bottom: 30px;
    @media all and (max-width: 1172px) {
      height: 60px;
    }
  }
  &__textarea {
    height: 208px;
    @media all and (max-width: 1172px) {
      height: 160px;
    }
  }
  &__button {
    width: 238px;
    height: 72px;
    background-color: #02a0e3;
    color: #ffffff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: Lato-Medium;
    font-size: 24px;
    box-shadow: 0px 4px 18px rgba(52, 119, 196, 0.35),
      inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    margin-top: 34px;
  }
  &__button:hover {
    background-color: #5eb0f3;
  }
  &__img {
    max-width: 530px;
    width: 100%;
  }
}
